@import url(https://fonts.googleapis.com/css2?family=Gaegu:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Rules{max-width:80vw;margin:2rem auto;border:2px dashed #dcdcdc;padding:2rem;border-radius:12px}.Rules .btn{display:inline-block}.Rules .btn.right{float:right;font-size:1.5rem;padding:0 1rem}

.TeamsModal .button-wrapper{display:flex;justify-content:center;flex-direction:column;align-items:center}.TeamsModal .teams-wrapper{display:flex;flex:1 1 auto}

.TeamSetup{flex:1 1;padding:0 2rem;display:flex;flex-direction:column;align-items:center}.TeamSetup input{display:block;margin-bottom:1rem}.TeamSetup .team-title{margin-top:0;display:flex;align-items:center}.TeamSetup .team-title .starting-team{display:inline-block;width:1rem;height:1rem;border-radius:50%;margin-left:0.5rem;cursor:pointer;opacity:0.7}.TeamSetup .team-title .starting-team.red{background-color:red}.TeamSetup .team-title .starting-team.red.active{box-shadow:0px 0px 7px 0px red}.TeamSetup .team-title .starting-team.blue{background-color:#0095ff}.TeamSetup .team-title .starting-team.blue.active{box-shadow:0px 0px 7px 0px #0095ff}.TeamSetup .team-title .starting-team.green{background-color:#00b106}.TeamSetup .team-title .starting-team.green.active{box-shadow:0px 0px 7px 0px #00b106}.TeamSetup .team-title .starting-team.active{opacity:1}.TeamSetup .player-wrap{position:relative}.TeamSetup form{display:flex;flex-direction:column;flex:1 1 auto;align-items:center}.TeamSetup .players{flex:1 1 auto;max-height:200px;margin-bottom:1rem;border-bottom:2px dashed;overflow:scroll}.TeamSetup .remove-player{position:absolute;top:50%;transform:translateY(-50%);right:0.5rem;background:none;border:none;font-family:inherit;cursor:pointer;font-size:1rem}

.ModalWrapper{width:100vw;height:100vh;position:fixed;display:flex;justify-content:center;align-items:center;z-index:1040;background:rgba(88,88,88,0.7)}.ModalWrapper .Modal{display:flex;flex-direction:column;justify-content:flex-end;background:#f9e5a9;border-radius:12px;border:3px solid;position:relative;padding:3rem;box-sizing:border-box;max-width:calc(100vw - 2rem);max-height:calc(100vh - 2rem)}.ModalWrapper .close-modal{position:absolute;right:1rem;top:1rem;font-size:2rem;background:none;border:none;font-weight:bold;cursor:pointer;font-family:inherit}.ModalWrapper .modal-title{font-size:2.5rem;text-align:center;margin-top:0}

.TeamScore{display:flex;flex-direction:column;align-items:center}.TeamScore .score{text-align:center;margin-bottom:0}

.ScoreBoard{position:absolute;background:#f9e5a9;z-index:5;border-bottom-right-radius:10px;border-bottom-left-radius:10px;border:2px solid;border-top:none;transition:all 0.2s;min-width:400px;top:0;left:50%;transform:translate(-50%, -100%)}.ScoreBoard:after{content:"^";position:absolute;left:50%;transform:translate(-50%, -60%) rotate(180deg);width:20px;display:flex;height:20px;align-items:center;justify-content:center;font-size:3rem;transition:all 0.2s;cursor:pointer}.ScoreBoard.expanded{top:0;left:50%;transform:translateX(-50%)}.ScoreBoard.expanded:after{opacity:0}.ScoreBoard .score-wrapper{display:flex;justify-content:center;padding:0 2rem 2rem}.ScoreBoard .score-wrapper .TeamScore:last-of-type{margin-left:5rem}

.Badge{display:inline-flex;align-items:center;justify-content:center;background:#f9e5a9;color:#000;font-size:1rem;font-weight:bold;margin:0.3rem;min-width:1rem;border-radius:8px;padding:3px}

.TeamSummary .team-title{margin-top:0}.TeamSummary .names{list-style:none;text-align:left;padding:0}.TeamSummary+.TeamSummary{margin-top:2rem}

.Menu{border-radius:6px;border:2px solid rgba(0,177,6,0.8);min-width:120px;padding:0;font-family:inherit}.Menu .menu-trigger{text-align:center}.Menu .menu-trigger:after{content:"⋀";margin-left:0.3rem}.Menu .menu-item{display:block;border-bottom:2px solid #00b106;color:#000;color:initial}.Menu .menu-item>*,.Menu .menu-trigger{display:block;color:#000;color:initial;font-size:1rem;font-weight:500;padding:0.5rem 1rem;cursor:pointer;transition:color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out}.Menu .menu-item>*:hover,.Menu .menu-trigger:hover{background-color:rgba(0,177,6,0.8);border-color:#00b106;color:white}.Menu.expanded .menu-trigger::after{content:"⋁"}

.CardContextMenu{background-color:white;border:2px solid #000;border-radius:5px}.CardContextMenu .menu-action{border-bottom:1px dashed;margin:0;padding:0.3rem 1rem;width:100%;cursor:pointer;font-family:inherit;font-size:1rem}.CardContextMenu .menu-action.red:hover{background-color:rgba(255,0,0,0.7)}.CardContextMenu .menu-action.blue:hover{background-color:rgba(0,149,255,0.7)}.CardContextMenu .menu-action.green:hover{background-color:rgba(0,177,6,0.7)}.CardContextMenu .menu-action.neutral:hover{background-color:rgba(255,212,0,0.7)}.CardContextMenu .menu-action.black:hover{background-color:rgba(0,0,0,0.7)}.CardContextMenu .menu-action:last-of-type{border-bottom:none}.CardContextMenu .menu-action:not(.neutral):hover{color:white;cursor:pointer;border-color:black}

.Badge,.Card.selected.duet.neutral:after{display:inline-flex;align-items:center;justify-content:center;background:#f9e5a9;color:#000;font-size:1rem;font-weight:bold;margin:0.3rem;min-width:1rem;border-radius:8px;padding:3px}.Card{border:1px solid;cursor:pointer;margin:0.5rem;position:relative;border-radius:12px}.Card .CardContextMenu{position:absolute;z-index:2;left:20%;top:20%;width:100%}.Card .card-id{left:0;position:absolute;top:0;height:15%;width:15%}.Card .card-img{width:100%;border-radius:12px;-webkit-user-select:none;-ms-user-select:none;user-select:none}.Card .card-img.enlarged{border:2px solid;left:-50%;position:absolute;top:-50%;width:200%;z-index:2}.Card.selected.red:after{background-color:rgba(255,0,0,0.5);border-color:red}.Card.selected.blue:after{background-color:rgba(0,149,255,0.5);border-color:#0095ff}.Card.selected.green:after{background-color:rgba(0,177,6,0.5);border-color:#00b106}.Card.selected.neutral:after{background-color:rgba(255,212,0,0.5);border-color:#ffd400}.Card.selected.black:after{background-color:rgba(0,0,0,0.5);border-color:#000}.Card.selected:after{content:"";border:4px solid;box-sizing:border-box;position:absolute;height:100%;width:100%;left:0;top:0;border-radius:12px}.Card.selected.duet.neutral:after{border:2px solid;height:15%;width:15%;left:unset;right:0;content:"x"}

.CardGrid{display:grid;grid-template-columns:250px 250px 250px 250px 250px;grid-template-rows:250px 250px 250px 250px}@media only screen and (max-height: 1000px){.CardGrid{grid-template-columns:220px 220px 220px 220px 220px;grid-template-rows:220px 220px 220px 220px}}@media only screen and (max-height: 800px){.CardGrid{grid-template-columns:25vh 25vh 25vh 25vh 25vh;grid-template-rows:25vh 25vh 25vh 25vh}}@media only screen and (max-width: 1300px){.CardGrid{grid-template-columns:16vw 16vw 16vw 16vw 16vw;grid-template-rows:16vw 16vw 16vw 16vw}}

.container{align-items:center;display:flex;min-height:100vh;justify-content:center;width:100%}.inner-container{align-items:center;display:flex;justify-content:center;width:100%;padding:1rem}.side-wrapper{display:flex;flex-direction:column;justify-content:flex-end;align-self:stretch;margin:0.5rem 0}.side-wrapper .teams-summary{flex:1 1 auto}

.SpyCard{display:flex;align-items:center}.SpyCard .spy-grid{background-color:black;border-radius:12px;display:grid;grid-template-columns:150px 150px 150px 150px 150px;grid-template-rows:125px 125px 125px 125px;padding:15px}.SpyCard .spy-grid .cell{border-radius:12px;margin:10px;display:flex;justify-content:center;align-items:center}.SpyCard .spy-grid .cell.red{background:red;background:radial-gradient(circle, #ff4d4d 0%, red 100%)}.SpyCard .spy-grid .cell.blue{background:#0095ff;background:radial-gradient(circle, #4db5ff 0%, #0095ff 100%)}.SpyCard .spy-grid .cell.green{background:#00b106;background:radial-gradient(circle, #00fe09 0%, #00b106 100%)}.SpyCard .spy-grid .cell.black{background:#292929;background:radial-gradient(circle, #4f4f4f 0%, #292929 100%)}.SpyCard .spy-grid .cell:before{font-weight:900;font-size:3rem}.SpyCard .spy-grid .cell.neutral{background-color:#daca9e}@media only screen and (max-width: 900px){.SpyCard .spy-grid{grid-template-columns:100px 100px 100px 100px 100px;grid-template-rows:75px 75px 75px 75px}}@media only screen and (max-width: 650px){.SpyCard .spy-grid{grid-template-columns:75px 75px 75px 75px 75px;grid-template-rows:55px 55px 55px 55px}}@media only screen and (max-width: 525px){.SpyCard .spy-grid{grid-template-columns:15vw 15vw 15vw 15vw 15vw;grid-template-rows:12vw 12vw 12vw 12vw;padding:10px}}.SpyCard .starting-color{border-radius:8px;height:100px;margin:15px;width:30px}.SpyCard .starting-color.red{background:red;background:radial-gradient(circle, #ff4d4d 0%, red 100%)}.SpyCard .starting-color.blue{background:#0095ff;background:radial-gradient(circle, #4db5ff 0%, #0095ff 100%)}.SpyCard .starting-color.green{background:#00b106;background:radial-gradient(circle, #00fe09 0%, #00b106 100%)}.SpyCard .starting-color.black{background:#292929;background:radial-gradient(circle, #4f4f4f 0%, #292929 100%)}@media only screen and (max-width: 650px){.SpyCard .starting-color{height:60px;margin:10px;width:20px}}

.SpyMaster .find-card-wrapper{text-align:center}.SpyMaster .find-card-wrapper .btn{display:inline-block}.SpyMaster input{margin:1rem}.SpyMaster .container.spy-master{flex-direction:column;min-height:80vh}.SpyMaster .title{text-align:center;font-size:3.5rem}@media only screen and (max-width: 900px){.SpyMaster .title{font-size:2.5rem}}@media only screen and (max-width: 525px){.SpyMaster .title{font-size:2rem}}

body{font-family:"Gaegu", cursive}:root{font-size:20px}a{text-decoration:none;color:#0095ff}.text-red{color:red}.text-blue{color:#0095ff}.text-green{color:#00b106}.btn{border-radius:6px;border:2px solid #000;color:#000;color:initial;cursor:pointer;display:block;font-size:1rem;font-weight:500;margin-top:1rem;padding:0.5rem 1rem;text-decoration:none;transition:color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;font-family:inherit}.btn.blue{border-color:rgba(0,149,255,0.8)}.btn.blue:hover{background-color:rgba(0,149,255,0.8);border-color:#0095ff}.btn.green{border-color:rgba(0,177,6,0.8)}.btn.green:hover{background-color:rgba(0,177,6,0.8);border-color:#00b106}.btn:hover{background-color:rgba(0,177,6,0.8);border-color:#00b106;color:white}button.no-style,button.link{background:none;border:none;padding:0;margin:0;font-size:inherit;font-family:inherit;cursor:pointer}button.link{color:#0095ff;padding-left:0.2rem}button.link:hover{text-decoration:underline}input{padding:0.5rem 1rem;border:2px dashed #dcdcdc;border-radius:4px;font-size:1rem;font-family:inherit}

